CAAA,0CiBwCT,ejB3CQ,CAAA,gDiBgDR,ejB/CQ,CAAA,KiBmDR,eACI,CAAA,cACA,CAAA,iBACA","sourcesContent":["// Importing google font\n@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap'); // UBUNTU FONT\n@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap'); // POPPINS FONT\n\n@import '@fontsource/roboto/300.css';\n@import '@fontsource/roboto/400.css';\n@import '@fontsource/roboto/500.css';\n@import '@fontsource/roboto/700.css';\n","@import '../abstract/variables.scss';\n\n.mode-text,\n.theme-toggle-button,\n.none {\n    display: none;\n}\n\nheader {\n    position: absolute;\n    top: 0;\n    left: 0;\n    padding: 30px 7% 0px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    z-index: 99;\n    animation: animHeader 1s linear forwards;\n    animation-delay: 1s;\n    opacity: 0;\n\n    @keyframes animHeader {\n        0% {\n            opacity: 0;\n            top: -50vh;\n        }\n\n        100% {\n            opacity: 1;\n            top: 0vh;\n        }\n    }\n\n    .container {\n        width: 100%;\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n\n        .logo {\n            display: inline-block;\n            max-width: 150px;\n        }\n\n        .navbar-items {\n            list-style: none;\n            padding: 0;\n            margin: 0;\n            display: flex;\n            align-items: center;\n            justify-content: flex-end;\n\n            .item {\n                display: inline-block;\n                margin-left: 30px;\n\n                a {\n                    font-size: 14px;\n                    font-weight: 500;\n                    line-height: 14px;\n                    color: white;\n                    text-transform: uppercase;\n                    letter-spacing: 2px;\n                    text-decoration: none;\n                    font-family: 'poppins', sans-serif;\n                }\n            }\n        }\n    }\n}\n\n.header-lang-bg .css-1yk1gt9-MuiInputBase-root-MuiOutlinedInput-root-MuiSelect-root,\n.css-1sumxir-MuiFormLabel-root-MuiInputLabel-root {\n    background: transparent;\n    color: white;\n    height: 35px;\n    // text-transform: uppercase;\n    font-size: 14px;\n    font-weight: bold;\n}\n\n.css-bpeome-MuiSvgIcon-root-MuiSelect-icon,\nsvg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.MuiSelect-icon.MuiSelect-iconOutlined.css-hfutr2-MuiSvgIcon-root-MuiSelect-icon {\n    color: white;\n}\n\n#lang-background-invoices {\n\n    & fieldset {\n        border: 0;\n    }\n}\n\ndiv#demo-select-small {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    text-transform: capitalize;\n}\n\ndiv#demo-select-small-out {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    text-transform: capitalize;\n    font-family: ubuntu;\n}\n\n@media (max-width: 767px) {\n    header {\n        background: $red;\n        padding-bottom: 20px;\n        position: relative;\n\n        .container {\n            max-width: 530px;\n            flex-direction: column;\n\n            .logo {\n                max-width: 100px;\n                margin-bottom: 20px;\n            }\n\n            .navbar-items {\n                .item {\n                    margin-left: 15px;\n\n                    a {\n                        font-size: 12px;\n                    }\n                }\n            }\n        }\n\n        .css-lu9khh-MuiSwitch-root,\n        .css-j204z7-MuiFormControlLabel-root {\n            margin-right: 0px;\n        }\n    }\n}\n\n.countryFlag {\n    width: 24px;\n    height: auto;\n    margin-right: 7px;\n}\n\n\n#lang-background-invoices {\n\n    &.lang-background-loginscreens {\n        background-color: #fff;\n        border-radius: 30px;\n    }\n\n}\n\n\n.header-lang-bg {\n    background: #fff;\n    border-radius: 100px;\n    padding-left: 16px;\n\n\n    #lang-background-invoices {\n        margin: 0;\n        margin-left: 8px;\n    }\n}\n","// Font Family Variables\n$font-Ubuntu: ubuntu;\n\n// Color Variables\n$black: #000000;\n$bShColor: #00000033;\n$formTitle: #344857;\n$subTitle: #858585;\n$label: #abaec5;\n$input: #11153b;\n$success: #39be12;\n$inputBorder: #75818a;\n$inputBorder2: #e8ebf3;\n$labelShrink: rgba(0, 0, 0, 0.6);\n$eyeIcon: #c9c9c94d;\n$tooltipTitle: #171717;\n$tooltipSvg: #0fef00;\n$tooltipSvg2: #7d7d7d;\n$tooltipText: #4a4a4a;\n$tooltipText2: #7d7d7d;\n$langBg: #3a4548;\n$actionInput: #f1f4f9;\n$formTitleDark: #092133;\n$voidBtn: #7b7b7b;\n$border2: #70707080;\n$billingDetailsBg: #F7F8FA;\n$gray: #00000029;\n$grey: #344857;\n$grey2: #3a4548;\n$grey3: #55595c;\n$grey4: #888;\n$grey5: #555;\n$white: #ffffff;\n$white2: #f1f1f1;\n$whiteLight: #f7f7f9;\n$whiteDull: #ced4da;\n$orange: #f57c52;\n$red: #d63548;\n$pink: #da291c33;\n$gradient-1: transparent linear-gradient(297deg, #f57c52 0%, #d63548 100%) 0% 0%\n  no-repeat padding-box;\n\n\n// Font size variables\n$xs-text: 0.75rem;\n$sm-text: 14px;\n$md-text: 1rem;\n$lg-text: 36px;\n$xl-text: 1.5rem;\n$xxl-text: 2rem;\n","// Basic css\n* {\n  box-sizing: border-box;\n}\nhtml,\nbody {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\nbody {\n  font-size: $sm-text;\n  font-family: $font-Ubuntu !important;\n}\nheader,\nsection,\nfooter {\n  width: 100%;\n  float: left;\n  clear: both;\n}\ninput,\nselect,\nbutton {\n  outline: none !important;\n  box-shadow: none !important;\n}\ninput,\nbutton,\nselect {\n  box-shadow: none !important;\n  outline: none !important;\n}\nimg {\n  max-width: 100%;\n}\n\n// Mixin for the Account Buttons\n@mixin button() {\n  width: 100%;\n  height: 70px;\n  background: transparent linear-gradient(297deg, $orange 0%, $red 100%) 0%\n    0% no-repeat padding-box;\n  mix-blend-mode: luminosity;\n  border-radius: 35px;\n  text-align: center;\n  font-weight: bold;\n  font-size: 18px;\n  line-height: 24px;\n  letter-spacing: -0.72px;\n  color: $white !important;\n  border: 0px;\n  text-transform: uppercase;\n  opacity: 0.5;\n  box-shadow: none !important;\n  outline: none !important;\n  &.btn-enable-style {\n    opacity: 1;\n    background: transparent linear-gradient(297deg, $orange 0%, $red 100%) 0%\n      0% no-repeat padding-box;\n    mix-blend-mode: normal;\n    cursor: pointer !important;\n    &:hover {\n      box-shadow: 0px 30px 60px $pink !important;\n    }\n  }\n}\n\n// Mixin for the Error messages\n@mixin error() {\n  text-align: center;\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 14px;\n  font-family: 'poppins', sans-serif;\n  letter-spacing: 0px;\n  color: $red;\n  justify-content: center !important;\n  margin: 0px;\n  opacity: 1;\n  display: none;\n  svg {\n    width: 20px;\n    height: 20px;\n    margin-right: 10px;\n  }\n}\n\n// Mixin for the flex\n@mixin theme($theme: space-between) {\n  display: flex;\n  align-items: center;\n  justify-content: $theme;\n}\n\n\n.iconCta {\n  height: 45px;\n  text-align: left;\n  font-weight: bold;\n  font-size: 12px;\n  line-height: 13px;\n  letter-spacing: 0px;\n  color: $red;\n  text-transform: uppercase;\n  opacity: 1;\n  background: $actionInput;\n  border: 1px solid $red;\n  border-radius: 100px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 10px 30px;\n  text-decoration: none;\n  cursor: pointer;\n  transition: all 0.5s ease-in-out;\n  opacity: 1;\n  &:hover {\n    background: $red !important;\n    span {\n      color: $white !important;\n      &.icon {\n        svg {\n          path {\n            fill: $white !important;\n            stroke: $white !important;\n          }\n        }\n      }\n    }\n  }\n  span {\n    display: inline-block;\n    &.icon {\n      margin-right: 10px;\n    }\n  }\n}\n","// styles for search input placeholders\n#sidebar-top > div > div > div.right__elementsItem.search__group > form > div > input::placeholder, #searchpro